																				targetsheet = me.down("[name=targetsheet]"),
																				// fieldvalue = me.down("[name=fieldvalue]"),
																				checked = ctrl.getValue();
																			  
																			// fieldvalue.allowBlank = checked;
																			targetsheet.setVisible(checked);
																			deletectrl.setVisible(checked);
