IG$.nlpEdIt = $s.extend($s.window, {
	
	modal: true,
	layout: "fit",
	closable: false,
	resizable:false,
	width: 600,
	height: 500,
	
	title: "Sentence",
	
	init_f: function() {
		var me = this;
		
		me._mc = me._mc || {};
		
		me.down("[name=txt_intent]").setValue(me._mc.intent);
		me.down("[name=cb_type]").setValue(me._mc.cb_type || "utterances");
		
		if (me._mc.cb_type == "utterances")
		{
			me.down("[name=utterances]").setValue(me._mc.utterances ? me._mc.utterances.join("\n") : "");
			me.down("[name=answers]").setValue(me._mc.answers ? me._mc.answers.join("\n") : "");
		}
		else if (me._mc.cb_type == "slotfill")
		{
			me.down("[name=mandatory]").setValue(me._mc.mandatory);
			me.down("[name=trim_entity]").setValue(me._mc.trim_entity);
			me.down("[name=slot_message]").setValue(me._mc.fill_message ? me._mc.fill_message.join("\n") : "");
		}
		else if (me._mc.cb_type == "trimentity")
		{
			me.down("[name=te_type]").setValue(me._mc.type);
			me.down("[name=te_option]").setValue(me._mc.option);
			me.down("[name=te_texts]").setValue(me._mc.enum_options && me._mc.enum_options.texts ? me._mc.enum_options.texts.join("\n") : "");
			me.down("[name=t_regex]").setValue(me._mc.regex);
			
			if (me._mc.trim_rules)
			{
				me.down("[name=grid_te_rules]").store.loadData(me._mc.trim_rules);
			}
		}
		else if (me._mc.cb_type == "htmlcontent")
		{
			me.down("[name=h_content]").setValue(me._mc.content);
		}
		else if (me._mc.cb_type == "synonim")
		{
			me.down("[name=h_synonim_rep]").setValue(me._mc.synonim);
			me.down("[name=h_synonim]").setValue(me._mc.content);
		}
	},
	
	_setV: function(cname, required) {
		var me = this,
			c = me.down("[name=" + cname + "]");
		
		if (!c.getValue())
		{
			if (required !== false)
				c.markInvalid("Required");
			return;
		}
		else
		{
			c.clearInvalid();
		}
		
		return c.getValue();
	},
	
	_confirm: function() {
		var me = this,
			err = 0,
			tintent,
			_mc = me._mc,
			t;
		
		tintent = me.down("[name=txt_intent]"); 
		
		tintent.clearInvalid();
		
		if (!tintent.getValue())
		{
			tintent.markInvalid("Required!");
			err = 1;
		}
		_mc.intent = tintent.getValue();
		
		t = me._setV("cb_type");
		if (!t)
		{
			err = 1;
		}
		else
		{
			_mc.cb_type = t;
		}
			
		if (me._mc.cb_type == "utterances")
		{
			t = me._setV("utterances");
			
			if (!t)
				err = 1;
			else
				me._mc.utterances = t.split("\n");
			
			t = me._setV("answers");
   			me._mc.answers = t ? t.split("\n") : null;
		}
		else if (me._mc.cb_type == "slotfill")
		{
			t = me._setV("mandatory", false);
			me._mc.mandatory = t;
			
			t = me._setV("trim_entity");
			
			if (!t)
				err = 1;
			else
				_mc.trim_entity = t;
			
			t = me._setV("slot_message");
			
			if (!t)
				err = 1;
			else
				_mc.fill_message = t.split("\n");
		}
		else if (me._mc.cb_type == "trimentity")
		{
			t = me._setV("te_type");
			
			if (!t)
				err = 1;
			else
				_mc.type = t;
			
			t = me._setV("te_option");
			if (!t)
				err = 1;
			else
				_mc.option = t;
			
			if (_mc.type == "enum")
			{
				t = me._setV("te_texts");
				
				_mc.enum_options = {};
				
				if (!t)
					err = 1;
				else
					_mc.enum_options.texts = t.split("\n");
			}
			else if (_mc.type == "regex")
			{
				t = me._setV("t_regex");
				if (!t)
					err = 1;
				else
					_mc.regex = t;
			}
			
			_mc.trim_rules = [];
			
			var i,
				grid_te_rules = me.down("[name=grid_te_rules]"),
				r1, rec;
			
			for (i=0; i < grid_te_rules.store.data.items.length; i++)
			{
				rec = grid_te_rules.store.data.items[i];
				
				r1 = {
					texts: rec.get("texts"),
					type: rec.get("type"),
					left_words: rec.get("left_words"),
					right_words: rec.get("right_words"),
					words: rec.get("words")
				};
				_mc.trim_rules.push(r1);
			}
		}
		else if (me._mc.cb_type == "htmlcontent")
		{
			t = me._setV("h_content");
			if (!t)
				err = 1;
			else
				_mc.content = t;
		}
		else if (me._mc.cb_type == "synonim")
		{
			t = me._setV("h_synonim");
			if (!t)
				err = 1;
			else
				_mc.content = t;

			t = me._setV("h_synonim_rep");
			if (!t)
				err = 1;
			else
				_mc.synonim = t;
		}
		
		if (err)
			return;
		
		me.callback && me.callback.execute(me._mc);
		me.close();
	},
	
	_update_rule: function(rec) {
		var me = this,
			dlg = new IG$.nlpEdItT({
				instance: me.instance,
				_mc: rec.data,
				callback: new IG$.callBackObj(me, function(data) {
					rec.refresh();
				})
			});
		IG$.showDlg(me, dlg);
	},
	
	initComponent : function() {
		var me = this;
		
		$s.apply(me, {
			padding: ig$.padding,
			border: 0,
			items: [
				{
					xtype: "panel",
					layout: "fit",
					padding: ig$.padding,
					autoScroll: true,
					items: [
						{
							xtype: "panel",
							layout: {
								type: "vbox",
								align: "stretch"
							},
							padding: ig$.padding,
							items: [
								{
									xtype: "fieldset",
									title: "General",
									layout: {
										type: "vbox",
										align: "stretch"
									},
									items: [
										{
											xtype: "container",
											name: "t_intent",
											layout: {
												type: "hbox",
												align: "stretch"
											},
											items: [
												{
													xtype: "textfield",
													name: "txt_intent",
													fieldLabel: IRm$.r1("B_INTENTS") // "Intents"
												}
											]
										},
										{
											xtype: "container",
											layout: {
												type: "hbox",
												align: "stretch"
											},
											items: [
												{
													xtype: "combobox",
													name: "cb_type",
													fieldLabel: IRm$.r1("B_TYPE"), // "Type",
													displayField: "name",
													valueField: "value",
													queryMode: 'local',
													editable: false,
													autoSelect: true,
													required: true,
													store: {
														field: ["name", "value"],
														data: [
															{name: "Utterances", value: "utterances"},
															{name: "Trim Entity", value: "trimentity"},
															{name: "Slot Fill", value: "slotfill"},
															{name: "Html Content", value: "htmlcontent"},
															{name: "Synonim", value: "synonim"}
														]
													},
													listeners: {
														change: function(cb, newValue, oldValue, eOpts) {
															var me = this,
																i,
																tname;
															
															for (i=0; i < cb.store.data.items.length; i++)
															{
																tname = cb.store.data.items[i].get("value");
																me.down("[name=ct_" + tname + "]").setVisible(tname == newValue);
															}
														},
														scope: this
													}
												},
												{
													xtype: "container",
													flex: 1
												}
											]
										}
									]
								},
								{
									xtype: "fieldset",
									title: "Utterances",
									name: "ct_utterances",
									hidden: true,
									layout: {
										type: "vbox",
										align: "stretch"
									},
									items: [
										{
											xtype: "textarea",
											name: "utterances",
											fieldLabel: IRm$.r1("L_UTTERANCE"), // "Utterances",
											labelPosition: "top",
											height: 200
										},
										{
											xtype: "textarea",
											name: "answers",
											fieldLabel: IRm$.r1("L_ANSWERS"), // "Answers",
											labelPosition: "top",
											height: 280
										}
									]
								},
								{
									xtype: "fieldset",
									title: "Slot Fill",
									name: "ct_slotfill",
									hidden: true,
									layout: {
										type: "vbox",
										align: "stretch"
									},
									items: [
										{
											xtype: "container",
											layout: {
												type: "hbox",
												align: "stretch"
											},
											items: [
												{
													xtype: "checkbox",
													name: "mandatory",
													fieldLabel: IRm$.r1("L_MANDATORY") // "Mandatory"
												},
												{
													xtype: "container",
													flex: 1
												}
											]
										},
										{
											xtype: "container",
											layout: {
												type: "hbox",
												align: "stretch"
											},
											items: [
												{
													xtype: "textfield",
													name: "trim_entity",
													fieldLabel: IRm$.r1("L_TRIM_TYPE"), // "Trim Entity",
													required: true
												},
												{
													xtype: "container",
													flex: 1
												}
											]
										},
										{
											xtype: "textarea",
											name: "slot_message",
											fieldLabel: IRm$.r1("L_MESSAGES"), // "Messages",
											labelPosition: "top",
											height: 250
										}
									]
								},
								{
									xtype: "fieldset",
									title: "Trim Entity",
									name: "ct_trimentity",
									hidden: true,
									layout: {
										type: "vbox",
										align: "stretch"
									},
									items: [
										{
											xtype: "container",
											layout: {
												type: "hbox",
												align: "stretch"
											},
											items: [
												{
													xtype: "combobox",
													name: "te_type",
													fieldLabel: IRm$.r1("L_TRIM_ENTRY_TYPE"), // "Trim Entity Type",
													displayField: "name",
													valueField: "value",
													queryMode: 'local',
													editable: false,
													autoSelect: true,
													required: true,
													store: {
														field: ["name", "value"],
														data: [
															{name: "Enumeration", value: "enum"},
															{name: "RegEx", value: "regex"},
															{name: "Trim Rule", value: "trim"}
														]
													},
													listeners: {
														change: function(cb, newValue, oldValue, eOpts) {
															var me = this,
																ct_trimentity = me.down("[name=ct_trimentity]"),
																i,
																tname;
															
															for (i=0; i < cb.store.data.items.length; i++)
															{
																tname = cb.store.data.items[i].get("value");
																ct_trimentity.down("[name=te_" + tname + "]").setVisible(tname == newValue);
															}
														},
														scope: this
													}
												},
												{
													xtype: "container",
													flex: 1
												}
											]
										},
										{
											xtype: "container",
											layout: {
												type: "hbox",
												align: "stretch"
											},
											items: [
												{
													xtype: "textfield",
													name: "te_option",
													fieldLabel: IRm$.r1("B_OPTION") // "Option"
												}
											]
										},
										{
											xtype: "container",
											name: "te_enum",
											hidden: true,
											layout: {
												type: "vbox",
												align: "stretch"
											},
											items: [
												{
													xtype: "textarea",
													name: "te_texts",
													fieldLabel: IRm$.r1("L_TEXTS"), // "Texts",
													labelPosition: "top",
													height: 200
												}
											]
										},
										{
											xtype: "container",
											name: "te_regex",
											hidden: true,
											layout: {
												type: "vbox",
												align: "stretch"
											},
											items: [
												{
													xtype: "textarea",
													name: "t_regex",
													fieldLabel: IRm$.r1("B_REGEX"), // "RegEx",
													labelPosition: "top",
													height: 200
												}
											]
										},
										{
											xtype: "container",
											name: "te_trim",
											hidden: true,
											layout: {
												type: "vbox",
												align: "stretch"
											},
											items: [
												{
													xtype: "gridpanel",
													height: 250,
													name: "grid_te_rules",
													store: {},
													columns: [
														{
															text: IRm$.r1("B_TYPE"), // "Type",
															dataIndex: "type",
															tdCls: "igc-td-link"
														},
														{
															text: IRm$.r1("L_LEFT_WORDS"), // "Left Words",
															dataIndex: "left_words"
														},
														{
															text: IRm$.r1("L_RIGHT_WORDS"), // "Right Words",
															dataIndex: "right_words"
														},
														{
															text: IRm$.r1("L_WORDS"), // "Words",
															dataIndex: "words"
														},
														{
															xtype: "actioncolumn",
															width: 60,
															items: [
																{
																	iconCls: "icon-grid-dbicon",
																	handler: function (grid, rowIndex, colIndex) {
																		var rec = grid.store.data.items[rowIndex];
																		me._update_rule(rec);
																	},
																	scope: this
																},
																{
																	iconCls: "icon-grid-delete",
																	tooltip: IRm$.r1("L_REMOVE"),
																	handler: function(grid, rowIndex, colIndex) {
																		var me = this,
																			store = grid.store,
																			rec = store.getAt(rowIndex);
																		store.removeAt(rowIndex);
																	},
																	scope: this
																}
															]
														}
													],
													listeners: {
														cellclick: function(view, node, c, rec, rindex, e) {
															var me = this;
															me._update_rule(rec);
														},
														scope: this
													},
													tbar: [
														{
															xtype: "button",
															text: IRm$.r1("L_ADD_NEW"),
															handler: function() {
																var me = this,
																	dlg = new IG$.nlpEdItT({
																		instance: me.instance,
																		callback: new IG$.callBackObj(me, function(data) {
																			var me = this,
																				grid_te_rules = me.down("[name=grid_te_rules]");
																			 
																			grid_te_rules.store.add(data);
																		})
																	});
																IG$.showDlg(me, dlg);
															},
															scope: this
														}
													]
												}
											]
										}
									]
								},
								{
									xtype: "fieldset",
									title: "Html Content",
									name: "ct_htmlcontent",
									hidden: true,
									layout: {
										type: "vbox",
										align: "stretch"
									},
									items: [
										{
											xtype: "textarea",
											name: "h_content",
											fieldLabel: IRm$.r1("B_CONTENTS"), // "Contents",
											labelPosition: "top",
											height: 300
										}
									]
								},
								{
									xtype: "fieldset",
									title: "Synonim Content",
									name: "ct_synonim",
									hidden: true,
									layout: {
										type: "vbox",
										align: "stretch"
									},
									items: [
										{
											xtype: "textfield",
											name: "h_synonim_rep",
											fieldLabel: "Representive"
										},
										{
											xtype: "textarea",
											name: "h_synonim",
											fieldLabel: IRm$.r1("B_CONTENTS"), // "Contents",
											labelPosition: "top",
											height: 300
										}
									]
								}
							]
						}
					]
				}
			],
			buttons:[
				"->",
				{
					text: IRm$.r1('B_CONFIRM'),
					bodyCls: "ig-btn-confirm",
					handler: function() {
						this._confirm();
					},
					scope: this
				}, 
				{
					text: IRm$.r1('B_CANCEL'),
					handler:function() {
						this.close();
					},
					scope: this
				}
			],

			listeners: {
				afterrender: function() {
					this.init_f();
				}
			}
		});
		
		IG$.nlpEdIt.superclass.initComponent.apply(this, arguments);
	}
});

IG$.nlpEdItT = $s.extend($s.window, {
	
	modal: true,
	layout: "fit",
	closable: false,
	resizable:false,
	width: 600,
	height: 500,
	
	title: "Sentence",
	
	i_fields: [
		{
			n: "te_trim_type",
			m: "type"
		},
		{
			n: "te_left_words",
			m: "left_words",
			a: 1,
			required: false
		},
		{
			n: "te_right_words",
			m: "right_words",
			a: 1,
			required: false
		},
		{
			n: "te_words",
			m: "words",
			a: 1,
			required: false
		}
	],
	
	init_f: function() {
		var me = this,
			_mc;
		
		me._mc = _mc = me._mc || {};
		
		$.each(me.i_fields, function(i, t) {
			var c = me.down("[name=" + t.n + "]"),
				v = _mc[t.m];
			
			if (t.a)
			{
				c.setValue(v ? v.join("\n") : "");
			}
			else
			{
				c.setValue(v);
			}
		});
		
		if (_mc.options)
		{
			if (_mc.options.skip && _mc.options.skip.length)
			{
				me.down("[name=te_skip]").setValue(_mc.options.skip.join("\n"));
			}
		}
	},
	
	_confirm: function() {
		var me = this,
			err = 0,
			_mc = me._mc,
			te_skip = me.down("[name=te_skip]");
			
		$.each(me.i_fields, function(i, t) {
			var c = me.down("[name=" + t.n + "]"),
				v = c.getValue(),
				required = true;
			
			if (t.m != "type")
			{
				required = false;
				
				if (_mc.type == "between")
				{
					if (t.m == "words")
					{
						required = false;
					}
				}
				else
				{
					if (t.m == "left_words" || t.m == "right_words")
					{
						required = false;
					}
				}
			}
			
			if (!v && required)
			{
				c.markInvalid();
				err = 1;
			}
			else
			{
				c.clearInvalid();
				
				if (t.a)
				{
					_mc[t.m] = v ? v.split("\n") : null;
				}
				else
				{
					_mc[t.m] = v;
				}
			}
		});
			
		if (err)
			return;
		
		_mc.options = {};
		
		if (te_skip.getValue())
		{
			_mc.options.skip = te_skip.getValue().split("\n");
		}
		
		me.callback && me.callback.execute(me._mc);
		me.close();
	},
	
	initComponent : function() {
		var me = this;
		
		$s.apply(me, {
			padding: ig$.padding,
			border: 0,
			items: [
				{
					xtype: "panel",
					layout: "fit",
					padding: ig$.padding,
					autoScroll: true,
					items: [
						{
							xtype: "panel",
							layout: {
								type: "vbox",
								align: "stretch"
							},
							padding: ig$.padding,
							items: [
								{
									xtype: "container",
									layout: {
										type: "hbox",
										align: "stretch"
									},
									items: [
										{
											xtype: "combobox",
											name: "te_trim_type",
											fieldLabel: IRm$.r1("L_TRIM_TYPE"), // "Trim Type",
											displayField: "name",
											valueField: "value",
											queryMode: 'local',
											editable: false,
											autoSelect: true,
											required: true,
											store: {
												field: ["name", "value"],
												data: [
													{name: "Between", value: "between"},
													{name: "After", value: "after"},
													{name: "AfterFirst", value: "afterfirst"},
													{name: "AfterLast", value: "afterlast"},
													{name: "Before", value: "before"},
													{name: "BeforeFirst", value: "beforefirst"},
													{name: "BeforeLast", value: "beforelast"}
												]
											},
											listeners: {
												change: function(cb, newValue, oldValue, eOpts) {
													var me = this;
													
													if (newValue == "between")
													{
														me.down("[name=te_words]").hide();
														me.down("[name=te_left_words]").show();
														me.down("[name=te_right_words]").show();
													}
													else if (newValue == "after" || newValue == "afterfirst")
													{
														me.down("[name=te_right_words]").show();
														me.down("[name=te_left_words]").hide();
													}
													else if (newValue == "before" || newValue == "beforefirst")
													{
														me.down("[name=te_right_words]").hide();
														me.down("[name=te_left_words]").show();
													}
													else
													{
														me.down("[name=te_words]").show();
														me.down("[name=te_left_words]").hide();
														me.down("[name=te_right_words]").hide();
													}
												},
												scope: this
											}
										},
										{
											xtype: "container",
											flex: 1
										}
									]
								},
								{
									xtype: "fieldset",
									title: "Options",
									layout: {
										type: "vbox",
										align: "stretch"
									},
									items: [
										{
											xtype: "textarea",
											fieldLabel: IRm$.r1("L_SKIP_WORDS"), // "Skip words",
											name: "te_skip",
											labelPosition: "top",
											height: 120
										}
									]
								},
								{
									xtype: "textarea",
									name: "te_left_words",
									fieldLabel: IRm$.r1("L_LEFT_WORDS"), // "Left Words",
									labelPosition: "top",
									height: 200
								},
								{
									xtype: "textarea",
									name: "te_right_words",
									fieldLabel: IRm$.r1("L_RIGHT_WORDS"), // "Right Words",
									labelPosition: "top",
									height: 200
								},
								{
									xtype: "textarea",
									name: "te_words",
									fieldLabel: IRm$.r1("L_WORDS"), // "Words",
									labelPosition: "top",
									height: 200
								}
							]
						}
					]
				}
									
			],
			buttons:[
				"->",
				{
					text: IRm$.r1('B_CONFIRM'),
					bodyCls: "ig-btn-confirm",
					handler: function() {
						this._confirm();
					},
					scope: this
				}, 
				{
					text: IRm$.r1('B_CANCEL'),
					handler:function() {
						this.close();
					},
					scope: this
				}
			],

			listeners: {
				afterrender: function() {
					this.init_f();
				}
			}
		});
		
		IG$.nlpEdItT.superclass.initComponent.apply(this, arguments);
	}
});