IG$.dlg_syscon = $s.extend($s.window, {
	width: 500,
	layout: "fit",
	modal: true,
	
	bodyPadding: ig$.padding,
	
	_init: function() {
		var me = this,
			meta_content = me.meta_content,
			txt_meta = me.down("[name=txt_meta]"),
			cnt;
			
		cnt = JSON.stringify(meta_content, null, 2);
			
		txt_meta.setValue(cnt);
	},
	
	_confirm: function() {
		var me = this,
			txt_meta = me.down("[name=txt_meta]"),
			data;
			
		data = JSON.parse(txt_meta.getValue());
		
		if (!data)
			return;
			
		IG$.confirmMessages(ig$.appname, IRm$.r1("L_SAVE_CHANGES"), function(e) {
			if (e == "yes")
			{
				me.callback && me.callback.execute(data);
				me.close();
			}
		});
	},
	
	initComponent: function() {
		var me = this;
		
		$s.apply(me, {
			title: IRm$.r1("L_EXPORT"),
			items: [
				{
					xtype: "panel",
					flex: 1,
					layout: {
						type: "fit"
					},
					defaults:{
						padding: ig$.padding
					},
					items: [
						{
							xtype: "panel",
							padding: ig$.padding,
							layout: {
								type: "vbox",
								align: "stretch"
							},
							items: [
								{
									xtype: "textarea",
									name: "txt_meta",
									height: 380
								}
							]
						}
					]
				}
			],
			buttons: [
				{
					xtype: "button",
					text: IRm$.r1("L_META_IMPORT"),
					handler: function() {
						this._confirm();
					},
					scope: this
				},
				{
					xtype: "button",
					text: IRm$.r1("B_CLOSE"),
					handler: function() {
						this.close();
					},
					scope: this
				}
			],
			listeners: {
				afterrender: function(tobj) {
					tobj._init();
				}
			}
		});
		
		IG$.dlg_syscon.superclass.initComponent.call(this);
	}
});