IG$.chart_dsgn = $s.extend(IG$.v$pnl, {
	padding: ig$.padding,
	closable: true,
	iconCls: "icon-ing-docdef",
	layout: "fit",
	
	init_f: function() {
		var me = this;
		me.load_cnt();		
	},
	
	load_cnt: function() {
		var me = this,
			req = new IG$._rpc$();
		
		req.init(me, 
			{
				ack: "helpcontent",
				payload: {uid: "/SYS_Chart_dsgn", name: "SYS_Chart_dsgn", type: "Folder", post: "create", duty: "everyone", mode: "s"},
				mbody: {option: "translate"}
			}, me, function(jdoc) {
				var me = this,
					req = new IG$._rpc$();

				me.uid = jdoc[0].uid;

				req.init(me, 
					{
						ack: "content",
						payload: {uid: me.uid},
						mbody: {}
					}, me, function(jdoc) {
						var me = this,
							grdprog = me.down("[name=grdprog]"),
							i, rec, attrb2;
						for (i=0; i < jdoc.children.length; i++)
						{
							rec = jdoc.children[i];
							if (rec.attributes && rec.attributes.attrb2)
							{
								attrb2 = rec.attributes.attrb2.split(";");
								rec.label = attrb2[0];
								rec.charttype = attrb2[1];
								rec.subtype = attrb2[2];
							}
						}
						grdprog.store.loadData(jdoc.children);
					}, null);
				req.send();
			}, null);
		req.send();
	},
	
	_editProg: function(rec) {
		var me = this,
			instance = me.instance,
			mainpanel = instance.mainPanel,
			grdprog = me.down("[name=grdprog]"),
			mdata;
		
		mdata = rec ? rec.data : {};
		mainpanel && mainpanel.navigateApp(mdata.uid, "syschart", mdata.name || "New ChartDesign", mdata.nodepath, false, true, {
			parent_uid: me.uid
		});
	},

	_exp: function() {
		var me = this;

		IG$.confirmMessages(IRm$.r1("T_EXP_MC"), IRm$.r1("T_EXP_MC_Q"), function(dlg) {
			var panel = me,
				meta,
				req = new IG$._rpc$(),
				i, rec,
				payload;
			
			meta = {
				uid: panel.uid,
				name: "SYS_Chart_dsgn",
				type: "Folder",
				nodepath: "/SYS_Chart_dsgn"
			};
				
			if (meta && meta.uid && dlg == "yes")
			{
				payload = {
					uid: meta.uid,
					nodepath: meta.nodepath,
					name: meta.name,
					type: meta.type
				};
				req.init(panel, 
					{
						ack: "content",
						payload: payload,
						mbody: {output: "file"}
					}, panel, function(jdoc) {
						var fpath, filename;
						
						if (jdoc)
						{
							fpath = jdoc.luid;
							filename = jdoc.filename;
							
							if (fpath && filename)
							{
								$.download(ig$.servlet + "/download", [
									{name: "ack", value: "download"},
									{name: "payload", value: fpath},
									{name: "mbody", value: filename},
									{name: "_mts_", value: (IG$._g$a || "")},
									{name: "session_key", value: (window.use_session_key ? ig$._session_key || "empty" : "")}
								], "POST");
							}
						}
					}, null);
				req.send();
			}
		}, me, me);
	},
	
	initComponent: function() {
		var me = this;
		
		$s.apply(this, {
			items: [
				{
					xtype: "panel",
					layout: {
						type: "vbox",
						align: "stretch"
					},
					items: [
						// card1 -- list of schedule
						{
							xtype: "gridpanel",
							flex: 1,
							name: "grdprog",
							store: {
								xtype: "store"
							},
							columns: [
								{
									text: IRm$.r1("B_NAME"),
									width: 200,
									dataIndex: "name"
								},
								{
									text: IRm$.r1("L_PKG_NAME"),
									flex: 1,
									dataIndex: "label"
								},
								{
									text: IRm$.r1("L_CHART_TYPE"),
									flex: 1,
									dataIndex: "charttype"
								},
								{
									text: IRm$.r1("L_CHART_SUBTYPE"),
									flex: 1,
									dataIndex: "subtype"
								},
								{
									xtype: "actioncolumn",
									width: 60,
									items: [
										{
											iconCls: "icon-grid-delete",
											tooltip: IRm$.r1('L_REMOVE'),
											handler: function (grid, rowIndex, colIndex) {
												var me = this,
													grd = grid,
													store = grd.store,
													rec = store.getAt(rowIndex);
												
												IG$.deleteMetaObject(rec.get("uid"), rec, new IG$.callBackObj(me, function() {
													store.removeAt(rowIndex);
												}));
											},
											scope: this
										}
									]
								}
							],
							listeners: {
								itemclick: function(tobj, record, item, index, e, eopts) {
									this._editProg(record);
								},
								scope: this
							},
							tbar: [
								{
									text: IRm$.r1("B_REFRESH"),
									handler: function() {
										this.init_f();
									},
									scope: this
								},
								{
									text: IRm$.r1("L_ADD_NEW_CHART"),
									handler: function() {
										this._editProg();
									},
									scope: this
								},
								"->",
								{
									text: IRm$.r1("L_EXPORT_META"),
									tooltip: IRm$.r1("L_EXPORT"),
									handler: function() {
										this._exp();
									},
									scope: this
								}
							]
						}
					]
				}
			]
		});
		
		IG$.chart_dsgn.superclass.initComponent.call(this);
	},
	listeners: {
		afterrender: function(tobj) {
			tobj.init_f();
		}
	}
});

IG$.chart_dsgn_edit = $s.extend(IG$.v$pnl, {
	padding: ig$.padding,
	closable: true,
	iconCls: "icon-ing-docdef",
	layout: "fit",
	no_history: true,

	init_f: function() {
		var me = this;

		var jdom = $(me.down("[name=script_0]").el.dom),
			jdomeditor = $(".idv-jcl-edtr", jdom),
			jview,
			editor, w, h;
		
		jdomeditor.empty();
		
		w = Math.max(IG$.j$ext._w(jdom) - 4, w || 380);
		h = Math.max(IG$.j$ext._h(jdom) - 4, h || 380);
		
		jview = $("<div class='idv-jcl-view'></div>").appendTo(jdomeditor);
		IG$.j$ext._w(jview, w);
		IG$.j$ext._h(jview, h);
		
		editor = IG$.ace_edit(jview[0], "javascript");
		
		me.scriptview_0 = jview;
		me.scripteditor_0 = editor;
		
		setTimeout(function() {
			me.load_cnt();
		}, 500);
	},

	load_cnt: function() {
		var me = this,
			req = new IG$._rpc$();
		
		if (!me.uid)
		{
			me._meta = {scripts: {}};
			me.rs_load_cnt();
		}
		else
		{
			req.init(me, 
				{
					ack: "content",
					payload: {address: me.uid},
					mbody: {}
				}, me, function(jdoc) {
					me._meta = jdoc.content;
					me._meta.name = jdoc.name;
					me.rs_load_cnt();
				}, null);
			req.send();
		}
	},

	rs_load_cnt: function() {
		var me = this,
			_meta = me._meta,
			pnl_inp = me.down("[name=pnl_inp]"),
			prog_part = me.down("[name=prog_part]");

		_meta.baselib = _meta.baselib || "IG$.cVis.base";
		_meta.scripts = _meta.scripts || {};

		pnl_inp.setAllValues(_meta);
		me._prog_ignore = true;
		prog_part.setValue("");
		me._prog_ignore = false;
		me.scripteditor_0.setValue("");
	},

	_apply_prog_part: function(ov) {
		var me = this,
			_meta = me._meta,
			prog_part = me.down("[name=prog_part]");

		if (me._prog_ignore)
			return;

		if (!ov)
		{
			ov = prog_part.getValue();
		}

		if (!ov)
			return;

		_meta.scripts = _meta.scripts || {};
		_meta.scripts[ov] = IG$._encodeVal(me.scripteditor_0.getValue());
	},

	_update_prog_part: function() {
		var me = this,
			_meta = me._meta,
			prog_part = me.down("[name=prog_part]"),
			ov = prog_part.getValue();

		if (!ov)
		{
			me.scripteditor_0.setValue("");
			return;
		}

		_meta.scripts = _meta.scripts || {};
		me.scripteditor_0.setValue(IG$._decodeVal(_meta.scripts[ov]));
	},

	save_cnt: function() {
		var me = this,
			_meta = me._meta,
			pnl_inp = me.down("[name=pnl_inp]"),
			req = new IG$._rpc$();

		me._apply_prog_part();

		if (pnl_inp.getAllValues(_meta))
			return;

		if (!me.parent_uid)
			return;

		_meta.meta_type = "syschart";

		req.init(me, 
			{
				ack: "writecontent",
				payload: {
					address: me.uid || "/SYS_Chart_dsgn/" + _meta.name, 
					name: _meta.name, 
					pid: me.uid ? null : me.parent_uid,
					type: "SysChart"
				},
				mbody: _meta,
				meta_type: "syschart"
			}, me, function(jdoc) {
				me.uid = jdoc.uid;
				me.setTitle(jdoc.name);

				var chart = _meta, 
					cscript = IG$.cdsgn_tmpl(chart),
					i;

				try
				{
					var dyn = new IG$.cDynScript("custom_chart_" + chart.subtype, 0);
					dyn.loadScript(cscript);
				}
				catch (e)
				{
					void 0;
				}

				var cchart = {
					label: chart.label,
					charttype: chart.charttype,
					subtype: chart.subtype,
					img: chart.img,
					grp: chart.grp,
					is_custom: true,
					uid: me.uid
				};

				ig$.custom_charts[cchart.subtype] = cchart;
				
				for (i=0; i < IG$.__chartoption.charttype.length; i++)
				{
					if (IG$.__chartoption.charttype[i].subtype == cchart.subtype)
					{
						IG$.__chartoption.charttype.splice(i, 1);
						break;
					}
				}
				IG$.__chartoption.charttype.push(cchart);

				IG$.alertmsg(ig$.appname, IRm$.r1("M_SAVED"), null, null, 0, "success");
			}, null);
		req.send();
	},
		
	initComponent: function() {
		var me = this,
			chartcateg = [{name: IRm$.r1("B_SELECT"), value: ""}],
			bcateg = IG$.__chartoption.chartcateg,
			i;

		for (i=0; i < bcateg.length; i++)
		{
			if (bcateg[i].value == "all")
				continue;

			chartcateg.push(bcateg[i]);
		}
		
		$s.apply(this, {
			items: [
				{
					xtype: "panel",
					layout: {
						type: "vbox",
						align: "stretch"
					},
					items: [
						{
							xtype: "panel",
							layout: "fit",
							flex: 1,
							tbar: [
								{
									iconCls: 'icon-toolbar-save',
									text: IRm$.r1('B_SAVE'),
									tooltip: IRm$.r1('B_SAVE'),
									handler: function() {
										this.save_cnt();
									},
									scope: this
								},
								{
									text: IRm$.r1('B_REFRESH'),
									handler:function() {
										var me = this;
										me.load_cnt();
									},
									scope: this
								},
								"->",
								{
									text: IRm$.r1('B_CLOSE'),
									handler:function() {
										var me = this;
										me.close();
									},
									scope: this
								}
							],
							items: [
								{
									xtype: "panel",
									padding: ig$.padding,
									layout: {
										type: "vbox",
										align: "stretch"
									},
									autoScroll: true,
									items: [
										
										{
											xtype: "panel",
											padding: ig$.padding,
											layout: {
												type: "vbox",
												align: "stretch"
											},
											name: "pnl_inp",
											title: IRm$.r1("B_GENERAL"),
											items: [
												
												{
													xtype: "container",
													layout: {
														type: "hbox",
														align: "stretch"
													},
													items: [
														{
															xtype: "container",
															layout: {
																type: "vbox",
																align: "stretch"
															},
															items: [
																{
																	xtype: "textfield",
																	fieldLabel: IRm$.r1("B_NAME"),
																	name: "name",
																	required: true
																},
																{
																	xtype: "textfield",
																	fieldLabel: IRm$.r1("L_CHART_TYPE"),
																	name: "charttype",
																	required: true
																},
																{
																	xtype: "textfield",
																	fieldLabel: IRm$.r1("L_CHART_IMG"),
																	name: "img",
																	required: true
																},
																{
																	xtype: "textfield",
																	fieldLabel: IRm$.r1("L_BASE_LIB"),
																	name: "baselib",
																	required: true,
																	emptyText: "IG$.cVis.base"
																}
															]
														},
														{
															xtype: "container",
															width: 10
														},
														{
															xtype: "container",
															flex: 1,
															layout: {
																type: "vbox",
																align: "stretch"
															},
															items: [
																{
																	xtype: "textfield",
																	fieldLabel: IRm$.r1("B_LABEL"),
																	name: "label",
																	required: true
																},
																{
																	xtype: "textfield",
																	fieldLabel: IRm$.r1("L_CHART_SUBTYPE"),
																	name: "subtype",
																	required: true
																},
																{
																	xtype: "combobox",
																	fieldLabel: IRm$.r1("L_CHART_GROUP"),
																	name: "grp",
																	required: true,
																	displayField: "name",
																	valueField: "value",
																	store: {
																		data: chartcateg
																	},
																	value: ""
																}
															]
														}
													]
												}
											]
										},
										{
											xtype: "fieldset",
											
											title: "Codes",
											layout: {
												type: "vbox",
												align: "stretch"
											},
											items: [
												{
													xtype: "panel",
													layout: {
														type: "vbox",
														align: "stretch"
													},
													height: 600,
													border: 0,
													items: [
														{
															xtype: "panel",
															layout: {
																type: "hbox",
																align: "stretch"
															},
															border: 0,
															items: [
																{
																	xtype: "combobox",
																	name: "prog_part",
																	fieldLabel: IRm$.r1("L_PROGRAM_PARTS"), // "Program Parts",
																	required: true,
																	displayField: "name",
																	valueField: "value",
																	store: {
																		data: [
																			{name: IRm$.r1("B_SELECT"), value: ""},
																			{name: "Code", value: "code_text"},
																			{name: "Draw Chart", value: "code_draw_chart"},
																			{name: "Update Chart", value: "code_update_disp"},
																			{name: "Dispose Chart", value: "code_dispose_disp"},
																			{name: "Export Chart", value: "code_export"}
																		]
																	},
																	listeners: {
																		change: function(tobj, nv, ov) {
																			var me = this;
																			if (ov)
																			{
																				me._apply_prog_part(ov);
																			}
																			me._update_prog_part();
																		},
																		scope: this
																	}
																},
																{
																	xtype: "container",
																	flex: 1
																},
																{
																	xtype: "button",
																	text: IRm$.r1("L_APPLY_CODES"),
																	handler: function() {
																		this._apply_prog_part();
																	},
																	scope: this
																}
															]
														},
														{
															name: "script_0",
															flex: 1,
															region: "center",
															html: "<div class='idv-jcl-edtr'></div>",
															split: true,
															border: 1,
															listeners: {
																resize: function(tobj, w, h, ow, oh, opts) {
																	if (me.scriptview_0 && me.scripteditor_0)
																	{
																		IG$.j$ext._w(me.scriptview_0, w - 4);
																		IG$.j$ext._h(me.scriptview_0, h - 4);
																		me.scripteditor_0.resize(true);
																	}
																}
															}
														}
													]
												}
											]
										}
									]
								}
							]
						}
					]
				}
			]
		});
		
		IG$.chart_dsgn_edit.superclass.initComponent.call(this);
	},
	listeners: {
		afterrender: function(tobj) {
			tobj.init_f();
		}
	}
});