IG$.bbs_categ = $s.extend($s.window, {
	
	modal: true,
	layout: "fit",
	closable: false,
	resizable:false,
	width: 300,
	autoHeight: true,

	viewmode: "newitem",
	
	_init: function() {
		var me = this;
		
		me.down("[name=fitemname]").focus();
	},
	
	commit_f: function(){
		var me = this,
			has_err,
			data = {};
		
		has_err = me.getAllValues(data);
		
		if (has_err)
			return;
		
		me.callback && me.callback.execute(data);
		
		me.close();
	},
	
	initComponent : function() {
		var me = this;

		me.title = me.title || IRm$.r1('L_MAKEITEM_G');

		var itemtypedesc = IRm$.r1("D_" + me.itemtype.toUpperCase());
		
		$s.apply(this, {
			items: [
				{
					xtype: "container",
					padding: ig$.padding,
					layout: {
						type: "vbox",
						align: "stretch"
					},
					items: 
					[
						{
							xtype: "displayfield",
							value: me.viewmode == "edititem" ? IRm$.r1('L_RENAME_ITEM', IG$._gitemname(me.itemname || "")) : IRm$.r1('L_MAKEITEM', itemtypedesc),
							name: "txttitle",
							height: 30,
							border: 0
						},
						{
							xtype: "panel",
							layout: 'anchor',
							border: 0,
							defaults: {
								anchor: '100%',
								labelWidth: 80
							},
							items: [
								{
									xtype: "textfield",
									fieldLabel: IRm$.r1('L_ITEMNAME_SHORT'),
									name: 'name',
									required: true,
									blankText: IRm$.r1("B_REQ")
								},
								{
									fieldLabel: IRm$.r1('B_DESC'),
									xtype: 'textarea',
									height: 50,
									required: true,
									name: 'title'
								}
							]
						}
					]
				}
			],
			buttons:[
				
				"->",
				{
					text: IRm$.r1('B_CONFIRM'),
					bodyCls: "ig-btn-confirm",
					handler: function() {
						var me = this;
						me.commit_f();
					},
					scope: this
				}, 
				{
					text: IRm$.r1('B_CANCEL'),
					handler:function() {
						this.close();
					},
					scope: this
				}
			],
			listeners: {
				afterrender: function(tobj) {
					tobj._init();
				}
			}
		});
		
		IG$.bbs_categ.superclass.initComponent.apply(this, arguments);
	}
});

IG$.mgrBBS = $s.extend(IG$.v$pnl, {
	scroll: false,
	closable: true,
	layout: "fit",
	
	init_f: function() {
		var me = this,
			bbscateg = me.down("[name=bbscateg]"),
			cuid = me.down("[name=cuid]"),
			req = new IG$._rpc$();
			
		me._init_file_ldr();
			
		req.init(me,
			{
				ack: "bbs",
				payload: {option: "list_category"},
				mbody: {}
			}, me, function(xdoc) {
				var dp = [{name: "Select All", uid: ""}],
					dp1 = [];
					
				$.each(xdoc, function(i, item) {
					dp.push({
						uid: item.uid,
						name: item.name || item.title,
						title: item.title
					});
					
					dp1.push({
						uid: item.uid,
						name: item.name || item.title,
						title: item.title
					});
				});
				bbscateg.store.loadData(dp);
				bbscateg.setValue("");
				
				cuid.store.loadData(dp1);
			}
		);
		
		req.send();
	},
	
	_init_file_ldr: function() {
		var me = this,
            d_file = me.down("[name=d_file]"),
            d_file_el = d_file.el.dom,
            m_fileupload = $(".m_fileupload", d_file_el),
            
            d_upfiles = me.down("[name=d_upfiles]");
            
        if (me.fileloader)
       	{
			// me.fileloader.fileupload("destroy");
		}
        
        m_fileupload.empty();
        
        me.fileloader = fileupload = $("<input type='file' name='files[]' data-url='upload' multiple></input>")
        	.appendTo(m_fileupload)
        	.hide();
        dropzone = $("<div class='filedropzone fade' style='height:80px'>Drop files here</div>")
        	.appendTo(m_fileupload);
        d_progress = $("<div class='file-progress'><div class='bar' style='width: 0%;'></div></div>").appendTo(m_fileupload);
        
        dropzone.bind("click", function(e) {
        	fileupload.trigger("click");
        });
        
        fileupload.fileupload({
            url: ig$.servlet + "/upload",
            dataType: "json",
            formData: {
				_mts_: IG$._g$a || ""
			},
            done: function (e, data) {
                var jdoc = data.result || {errorcode: "0xffff", errormsg: "Server incorrect responding"},
                    errcode = jdoc.errorcode,
                    tnodes,
                    i, dp = [], p, rec;
                
                if (errcode)
                {
                    IG$.ShowErrorMessage(jdoc, me, null);
                }
                else
                {
                    if (jdoc.result)
                    {
                        tnodes = jdoc.result.length ? jdoc.result : [jdoc.result];
                        
                        for (i=0; i < tnodes.length; i++)
                        {
                            p = tnodes[i];
                            dp.push(p);
                        }
                    }
                }
                
                if (dp && dp.length)
                {
                    d_upfiles.show();
                    
                    for (i=0; i < dp.length; i++)
                    {
                        rec = d_upfiles.store.add(dp[i]);
                        d_upfiles.selModel.select(rec, true);
                    }
                }
            },
            progressall: function (e, data) {
                var progress = parseInt(data.loaded / data.total * 100, 10),
                    bar = $(".bar", d_progress);
                bar.css(
                    "width",
                    progress + "%"
                );
                bar.text("Loaded " + data.loaded + " / " + data.total);
            },
     
            dropZone: dropzone
        }).bind('fileuploadsubmit', function (e, data) {
        });
        
        fileupload.fileupload("option", "url", ig$.servlet + "/upload");
	},
	
	_load_bbs: function(cuid) {
		var me = this,
			bbs_list = me.down("[name=bbs_list]"),
			req = new IG$._rpc$();
			
		req.init(me,
			{
				ack: "bbs",
				payload: {option: "list", cuid: cuid},
				mbody: {}
			}, me, function(xdoc) {
				bbs_list.store.loadData(xdoc);
			}
		);
		
		req.send();
	},
	
	view_bbs: function(rec) {
		var me = this,
			p_view = me.down("[name=p_view]"),
			bbs_list = me.down("[name=bbs_list]"),
			// d_content = p_view.down("[name=d_content]"),
			d_upfiles = me.down("[name=d_upfiles]"),
			req,
			form_init;
			
		bbs_list.hide();
		p_view.show();
		d_upfiles.hide();
		
		form_init = function(data) {
			me._init_file_ldr();
			
			me._edit_item = data;
			
			p_view.setAllValues(data);
			d_upfiles.store.loadData([]);
			
			/*
			if (me._editor)
			{
				me._editor.summernote("code", data.content || "");
			}
			*/
			
			var files = [],
				r,
				i;
			
			if (data.files)
			{
				for (i=0; i < data.files.length; i++)
				{
					r = data.files[i];
					files.push({
						uid: r.fuid,
						filename_org: r.fname
					});
				}
				
				d_upfiles.store.loadData(files);
				d_upfiles.show();
			}
		};
		
		if (!me._editor)
		{
			/*
			me._editor = $("#note_editor", d_content.body.dom).summernote({
				placeholder: "write here...",
				disableResizeEditor: true,
				minHeight: 400
			});
			*/
		}
		
		if (rec && rec.get("uid"))
		{
			req = new IG$._rpc$();
			req.init(null, 
				{
					ack: "bbs",
					payload: {option: "get", uid: rec.get("uid")},
					mbody: {}
				}, null, function(jdoc) {
					form_init(jdoc);
				}
			);
			
			req.send();
		}
		else
		{
			form_init({title: ""});
		}
	},
	
	view_list: function() {
		var me = this,
			p_view = me.down("[name=p_view]"),
			bbs_list = me.down("[name=bbs_list]");
			
		p_view.hide();
		bbs_list.show();
	},
	
	save_bbs: function() {
		var me = this,
			p_view = me.down("[name=p_view]"),
			d_upfiles = me.down("[name=d_upfiles]"),
			store = d_upfiles.store,
			data = me._edit_item,
			i, rec,
			has_err,
			req;
			
		has_err = p_view.getAllValues(data);
		
		if (has_err)
			return;
		
		/*	
		data.content = me._editor.summernote("code");
		
		if (!data.content)
		{
			IG$.alertmsg(ig$.appname, "Content is required!", null, null, 0, "error");
			return;
		}
		*/
		
		data.files = [];
		
		for (i=0; i < store.data.items.length; i++)
		{
			rec = store.data.items[i];
			
			data.files.push({
				fuid: rec.get("uid"),
				fname: rec.get("filename_org")
			});
		}
		
		req = new IG$._rpc$();
		req.init(me, 
			{
				ack: "bbs",
				payload: {option: "save"},
				mbody: data,
				meta_type: "bbs"
			}, null, function(jdoc) {
				me.view_list();
				me._load_bbs();
			}
		);
		
		req.send();
	},
	
	_add_categ: function(data) {
		var me = this,
			req;
			
		req = new IG$._rpc$();
		req.init(me, 
			{
				ack: "bbs",
				payload: {option: "add_categ", name: data.name, title: data.title},
				mbody: {}
			}, null, function(jdoc) {
				me.init_f();
			}
		);
		
		req.send();
	},
	
	_del_categ: function(bbsid) {
		var me = this,
			req;
			
		req = new IG$._rpc$();
		req.init(me, 
			{
				ack: "bbs",
				payload: {option: "del_categ", cuid: bbsid},
				mbody: {}
			}, null, function(jdoc) {
				me.init_f();
			}
		);
		
		req.send();
	},
	
	_download_file: function(rec) {
		var me = this,
			fileuid = rec.get("uid"),
			fname = rec.get("filename_org");
		
		$.download(ig$.servlet + "/download", [
			{name: "ack", value: "download"},
			{name: "payload", value: fileuid},
			{name: "_mts_", value: IG$._g$a || ""},
			{name: "mbody", value: fname},
			{name: "mtype", value: "import"},
			{name: "session_key", value: (window.use_session_key ? ig$._session_key || "empty" : "")}
		], "POST");
	},
	
	initComponent: function() {
		var me = this;
		
		$s.apply(this, {
			items: [
				{
					xtype: "panel",
					layout: {
						type: "vbox",
						align: "stretch"
					},
					padding: ig$.padding,
					flex: 1,
					items: [
						{
							xtype: "gridpanel",
							name: "bbs_list",
							store: {},
							border: 0,
							flex: 1,
							columns: [
								{
									xtype: "gridcolumn",
									text: IRm$.r1("L_CATEGORY"),
									width: 200,
									dataIndex: "categ"
								},
								{
									xtype: "gridcolumn",
									text: IRm$.r1("B_TITLE"),
									dataIndex: "title",
									tdCls: "igc-td-link",
									flex: 1
								},
								{
									text: IRm$.r1("B_DATE"),
									width: 150,
									sortable: false,
									dataIndex: "mdatefm"
								},
								{
									xtype: "gridcolumn",
									text: IRm$.r1("B_STATUS"),
									dataIndex: "active",
									width: 50
								},
								{
									xtype: "actioncolumn",
									width: 30,
									items: [
										
										{
											iconCls: "icon-grid-delete",
											tooltip: IRm$.r1("L_REMOVE"),
											handler: function(grid, rowIndex, colIndex) {
												var me = this,
													store = grid.store,
													rec = store.getAt(rowIndex);

												IG$.deleteMetaObject(rec.get("uid"), rec, new IG$.callBackObj(me, function() {
													store.removeAt(rowIndex);
												}));
											},
											scope: this
										}
									]
								}
							],
							listeners: {
								cellclick: function(view, node, c, rec, rindex, e) {
									c == 1 && this.view_bbs(rec);
								},
								scope: this
							},
							tbar: [
								
								{
									xtype: "combobox",
									displayField: "name",
									valueField: "uid",
									name: "bbscateg",
									fieldLabel: IRm$.r1("L_CATEGORY"), // "Category",
									queryMode: 'local',
									editable: false,
									autoSelect: true,
									store: {
										fields: [
											"name", "uid"
										]
									},
									listeners: {
										change: function(cb, newValue, oldValue, eOpts) {
											var me = this;
											me._load_bbs(newValue);
										},
										scope: this
									}
								},
								{
									text: "Add Category",
									handler: function() {
										var dlg = new IG$.bbs_categ({
											instance: me.instance,
											itemtype: "BBSCateg",
											callback: new IG$.callBackObj(me, function(data) {
												me._add_categ(data);
											})
										});
										IG$.checkLogin(this, dlg);
									},
									scope: this
								},
								{
									text: "Edit Category",
									handler: function() {
										var dlg = new IG$.bbs_categ({
											instance: me.instance,
											itemtype: "BBSCateg",
											callback: new IG$.callBackObj(me, function(data) {
												me._add_categ(data);
											})
										});
										IG$.checkLogin(this, dlg);
									},
									scope: this
								},
								{
									text: "Remove Category",
									handler: function() {
										var me = this,
											bbscateg = me.down("[name=bbscateg]");
											
										if (!bbscateg.getValue())
											return;
											
										IG$.confirmMessages(ig$.appname, IRm$.r1("L_C_SCH"), function(e) {
											if (e == "yes")
											{
												me._del_categ(bbscateg.getValue());
											}
										});
									},
									scope: this
								},
								"->",
								{
									text: "Write Content",
									handler: function() {
										this.view_bbs();
									},
									scope: this
								}
							]
						},
						{
							xtype: "panel",
							hidden: true,
							name: "p_view",
							autoScroll: false,
							layout: "fit",
							items: [
								{
									xtype: "panel",
									autoScroll: true,
									layout: "anchor",
									items: [
										{
											xtype: "combobox",
											required: true,
											displayField: "name",
											valueField: "uid",
											name: "cuid",
											fieldLabel: IRm$.r1("L_CATEGORY"), // "Category",
											queryMode: 'local',
											editable: false,
											autoSelect: true,
											store: {
												fields: [
													"name", "uid"
												]
											}
										},
										{
											xtype: "textfield",
											name: "title",
											required: true,
											fieldLabel: IRm$.r1("B_TITLE")
										},
										{
											xtype: "textarea",
											name: "content",
											height: 550,
											required: true,
											fieldLabel: IRm$.r1("B_CONTENT")
										},
										
										{
				                            xtype: "fieldset",
				                            layout: {
				                                type: "vbox",
				                                align: "stretch"
				                            },
				                            title: "File upload",
				                            items: [
				                                {
				                                    html: "<div class='m_fileupload'></div>",
				                                    name: "d_file",
				                                    height: 120,
				                                    bodyPadding: ig$.padding
				                                },
				                                {
				                                    name: "d_upfiles",
				                                    xtype: "gridpanel",
				                                    height: 120,
				                                    hidden: true,
				                                    selType: "checkboxmodel",
				                                    selModel:  {
				                                        mode: "MULTI"
				                                    },
				                                    store: {
				                                        fields: []
				                                    },
				                                    columns: [
				                                        {
				                                            text: IRm$.r1("L_FILENAME"),
				                                            dataIndex: "filename_org",
				                                            flex: 1
				                                        },
				                                        {
				                                            xtype: 'actioncolumn',
				                                            width: 20,
				                                            items: [
				                                                {
				                                                    iconCls: "icon-grid-delete",
				                                                    tooltip: 'Delete item',
				                                                    handler: function (grid, rowIndex, colIndex) {
				                                                        var rec = grid.store.getAt(rowIndex);
				                                                        grid.store.remove(rec);
				                                                    }
				                                                }
				                                            ]
				                                        }
				                                    ],
				                                    listeners: {
														cellclick: function(view, node, c, rec, rindex, e) {
															c == 1 && this._download_file(rec);
														},
														scope: this
													}
				                                }
				                            ]
				                        },
				                        {
				                        	xtype: "container",
				                        	height: 20
				                        },
										{
											xtype: "container",
											layout: {
												type: "hbox",
												align: "stretch"
											},
											items: [
												{
													xtype: "container",
													flex: 1
												},
												{
													xtype: "button",
													text: "Confirm",
													handler: function() {
														this.save_bbs();
													},
													scope: this
												},
												{
													xtype: "button",
													text: "Cancel",
													handler: function() {
														this.view_list();
													},
													scope: this
												},
												{
													xtype: "container",
													flex: 1
												}
											]
										}
									]
								}
							]
						}
					]
				}
			],
			listeners: {
				afterrender: function() {
					this.init_f();
				},
				scope: this
			}
		});
		
		IG$.mgrBBS.superclass.initComponent.call(this);
	}
});