IG$.metaImport = $s.extend($s.panel, {
	closable: true,
	
	iconCls: "icon-ing-docdef",
	
	layout: {
		type: "vbox",
		align: "stretch"
	},
	defaults: {
		anchor: "100%"
	},
	
	uploadFile: function() {
		var me = this,
			mform = this.down('[name=mform]');

		if (mform.getForm().isValid()) {
			mform.getForm().submit({
				url: ig$.servlet + "/upload",
				waitMsg: 'Uploading your data file',
				success: function(fp, o) {
					var node = fp.errorReader.jsonData;
					
					if (node.errorcode)
					{
						IG$.ShowErrorMessage(node, me, null);
						return;
					}
					
					var uid = node ? node.result.uid : null;
					
					mform.customowner.processUploadedFile(uid);
				},
				failure: function(fp, o) {
				}
			});
		}
	},
	
	processUploadedFile: function(uid) {
		var panel = this,
			uploadset = this.down("[name=uploadset]"),
			req = new IG$._rpc$();
			
		panel.uid = uid;
		
		req.init(panel, 
			{
				ack: "uploadcontent",
				payload: {uid: uid},
				mbody: {option: "meta_imp"}
			}, panel, panel.rs_processUploadedFile, null);
		
		req.send();
	},
	
	rs_processUploadedFile: function(jdoc) {
		var panel = this,
			i,
			metadata = panel.down("[name=metadata]"),
			metastore = metadata.store,
			d, 
			typename;
		
		for (i=0; i < jdoc.length; i++)
		{
			d = jdoc[i];
			typename = d.type.toLowerCase();
			d.iconCls = IG$.get_metatype_icon(typename, d);
		}
		
		metastore.loadData(jdoc);
	},
	
	toolbarHandler: function(cmd) {
		var panel = this,
			me = panel;
		
		switch (cmd)
		{
		case "cmd_imp":
			if (panel.uid)
			{
				IG$.confirmMessages("ImportMeta", IRm$.r1("M_CONFIRM_IMP"), me.do_importMeta, me, me);
			}
			else
			{
				IG$.ShowError(IRm$.r1("E_SEL_TGT_ITEM"),me);
			}
			break;
		}
	},
	
	do_importMeta: function(dlg) {
		if (dlg == "yes")
		{
			var panel = this,
				i,
				metadata = panel.down("[name=metadata]"),
				opt_imp = panel.down("[name=opt_imp]"),
				store = metadata.store,
				selvalues = metadata.getSelectionModel().selected,
				item, d,
				payload,
				req = new IG$._rpc$(), content = [];
			
			if (selvalues && selvalues.length > 0)
			{
				for (i=0; i < selvalues.length; i++)
				{
					d = selvalues.items[i];
					item = {
						name: d.get("name"),
						uid: d.get("uid"),
						nodepath: d.get("nodepath"),
						type: d.get("type")
					};
					content.push(item);
				}
				
				payload = {uid: panel.uid, option: "imp_load"};
				
				opt_imp.getAllValues(payload, true);
				
				req.init(panel, 
					{
						ack: "uploadcontent",
						payload: payload,
						mbody: {children: content},
						meta_type: "metrics"
					}, panel, function(jdoc, ucontents) {
						var me = this,
							tnode = jdoc,
							metadata = me.down("[name=metadata]"),
							store = metadata.store,
							errcnt = 0, errcode;
						
						if (tnode)
						{
							errcnt += me.updNodeRs(tnode, store);
						}
						
						if (errcnt > 0)
						{
							IG$.alertmsg(ig$.appname, "" + errcnt + " item failed to import!", null, null, 1, "error");
						}
						else
						{
							IG$.alertmsg(ig$.appname, IRm$.r1('M_SAVED'), null, null, 0, "success");
						}
					}, null);
				
				req.send();
			}
			else
			{
				IG$.ShowError(IRm$.r1("E_SEL_TGT_ITEM"),me);
			}
		}
	},
	
	updNodeRs: function(tnode, store) {
		var me = this,
			i, uid,
			errcnt = 0,
			errcode,
			rec;
		
		for (i=0; i < store.data.items.length; i++)
		{
			rec = store.data.items[i];
			uid = rec.get("uid");
			
			if (uid && tnode[uid])
			{
				errcode = parseInt(tnode[uid]);
				switch (errcode)
				{
				case 0:
					msg = "Not processed";
					break;
				case 1:
					msg = "Success";
					break;
				case 5:
					msg = "STAT_CONTENT_MISSING";
					break;
				case 6:
					msg = "STAT_FAIL_PARENT";
					break;
				case 7:
					msg = "STAT_UPPER_NODE_MISSING";
					break;
				case 8:
					msg = "STAT_NO_PARENT";
					break;
				default:
					msg = "Unknown " + t.status;
					break;
				}
				
				rec.set("logresult", msg);
				
				if (errcode != 1)
				{
					errcnt++;
				}
			}
		}
		
		return errcnt;
	},
		
	initComponent: function() {
		var panel = this;

		// ig$.$l_ = ig$.$l_ || 0;

		$s.apply(this, {
			padding: ig$.padding,
			items: [
				{
					xtype: "fieldset",
					title: IRm$.r1("L_LOAD_FILE"), // "Load File",
					region: "north",
					name: "uploadset",
					layout: "anchor",
					collapsible: true,
					items: [
						{
							xtype: 'form',
							name: 'mform',
							border: 0,
							customowner: this,
							layout: {
								type: "hbox",
								align: "stretch"
							},
							items: [
								{
									xtype: "hiddenfield",
									name: "_mts_",
									value: IG$._g$a
								},
								{
									xtype: 'fileuploadfield',
									name: 'photo',
									flex: 1,
									  fieldLabel: IRm$.r1("L_DATA"),
									  msgTarget: 'side',
									  allowBlank: false,
									  anchor: '100%',
									  buttonText: IRm$.r1("L_SELECT_FILE"),
									  buttonConfig: {
										  margin: "0 2 0"
									  }
								},
								{
									xtype: "button",
									text: IRm$.r1("L_UPLOAD"),
									handler: function() {
										panel.uploadFile();
									}
								}
							]
						}
					]
				},
				{
					xtype: "fieldset",
					layout: {
						type: "vbox",
						align: "stretch"
					},
					title: IRm$.r1("B_OPTION"),
					flex: 1,
					border: 0,
					items: [
						{
							xtype: "fieldcontainer",
							border: 0,
							layout: {
								type: "hbox",
								align: "stretch"
							},
							items: [
								{
									xtype: "container",
									layout: {
										type: "vbox",
										align: "stretch"
									},
									flex: 1,
									name: "opt_imp",
									// hidden: ig$.$l_ > 5,
									items: [
										{
											xtype: "checkbox",
											name: "opt_keep_date",
											fieldLabel: IRm$.r1("L_KEEP_MODI_DATES"),
											boxLabel: IRm$.r1("B_ENABLED"),
											checked: true
										},
										{
											xtype: "checkbox",
											name: "opt_owner_info",
											fieldLabel: IRm$.r1("L_KEEP_OWNER_INFO"),
											boxLabel: IRm$.r1("B_ENABLED"),
											checked: true
										},
										{
											xtype: "checkbox",
											name: "opt_security",
											fieldLabel: IRm$.r1("L_IMP_SECURITY"),
											boxLabel: IRm$.r1("B_ENABLED")
										},
										{
											xtype: "checkbox",
											name: "opt_history",
											fieldLabel: IRm$.r1("L_CLEAR_HIST"),
											boxLabel: IRm$.r1("B_ENABLED") + " (For Production)"
										}
									]
								},
								{
									xtype: 'button', 
									  plain: true,
									  text: IRm$.r1("L_IMPORT_SELECTED"),
									  handler: function() {
										  this.toolbarHandler('cmd_imp'); 
									  },
									  scope: this 
								}
							]
						}
					]
				},
				{
					xtype: "gridpanel",
					flex: 1,
					name: "metadata",
					store: {
						fields: []
					},
					
					selType: "checkboxmodel",
					selModel: {
						mode: "MULTI"
					},
					columns: [
						{
							width: 320,
							text: IRm$.r1("B_NAME"),
							dataIndex: "name"
						},
						{
							width: 70,
							text: IRm$.r1("B_EXISTS"),
							dataIndex: "exist"
						},
						{
							text: IRm$.r1("B_TYPE"),
							dataIndex: "type",
							width: 80
						},
						{
							flex: 1,
							text: IRm$.r1("B_PATH"),
							dataIndex: "nodepath"
						},
						{
							flex: 1,
							text: IRm$.r1("L_LOG_RESULT"),
							dataIndex: "logresult"
						}
					]
				}
			]
		});
		
		IG$.metaImport.superclass.initComponent.apply(this, arguments);
	},
	
	listeners: {
		afterrender: function() {
			var panel = this,
				mform = panel.down("[name=mform]");
			mform.getForm().errorReader = new IG$.m2ER();
			mform.customowner = panel;
		}
	}
});

IG$.metaImportDlg = $s.extend($s.window, {
	
	
	width: 400,
	autoHeight: true,
	
	layout: 'fit',
		
	initComponent: function() {
		$s.apply(this, {
			title: IRm$.r1('T_META_IMP'),
			items: [
				new IG$.metaImport()
			],
			buttons: [
				{
					text: IRm$.r1('B_CLOSE'),
					handler: function() {
						this.close();
					},
					scope: this
				}
			]
		});
		
		IG$.metaImportDlg.superclass.initComponent.apply(this, arguments);
	}
});