IG$.mgrNotice = $s.extend(IG$.v$pnl, {
	scroll: false,
	closable: true,
	layout: "fit",
	
	init_f: function() {
		var me = this;
		me._load_notice();
	},
	
	_load_notice: function() {
		var me = this,
			notice_list = me.down("[name=notice_list]"),
			req = new IG$._rpc$();
			
		req.init(me,
			{
				ack: "notice",
				payload: {option: "list"},
				mbody: {}
			}, me, function(xdoc) {
				notice_list.store.loadData(xdoc);
			}
		);
		
		req.send();
	},
	
	view_notice: function(rec) {
		var me = this,
			p_view = me.down("[name=p_view]"),
			notice_list = me.down("[name=notice_list]"),
			d_content = p_view.down("[name=d_content]"),
			req,
			form_init;
			
		notice_list.hide();
		p_view.show();
		
		form_init = function(data) {
			me._edit_item = data;
			
			var d = data.fromdate;
			
			if (d)
			{
				data.dt_start = new Date(d.substring(0, 4), Number(d.substring(4, 6)) - 1, Number(d.substring(6, 8)));
				data.dt_start_hh = Number(d.substring(8, 10));
				data.dt_start_mm = Number(d.substring(10, 12));
			}
			else
			{
				data.dt_start = new Date();
				data.dt_start_hh = 0;
				data.dt_start_mm = 0;
			}
			
			d = data.todate;
			
			if (d)
			{
				data.dt_end = new Date(d.substring(0, 4), Number(d.substring(4, 6)) - 1, Number(d.substring(6, 8)));
				data.dt_end_hh = Number(d.substring(8, 10));
				data.dt_end_mm = Number(d.substring(10, 12));
			}
			else
			{
				data.dt_end = new Date();
				data.dt_end_hh = 0;
				data.dt_end_mm = 0;
			}
			
			p_view.setAllValues(data);
			
			if (me._editor)
			{
				me._editor.summernote("code", data.content || "");
			}
		};
		
		if (!me._editor)
		{
			me._editor = $(d_content.body.dom).summernote({
				placeholder: "write here...",
				disableResizeEditor: true,
				minHeight: 400
			});
		}
		
		if (rec && rec.get("uid"))
		{
			req = new IG$._rpc$();
			req.init(null, 
				{
					ack: "notice",
					payload: {option: "get", uid: rec.get("uid")},
					mbody: {}
				}, null, function(jdoc) {
					form_init(jdoc);
				}
			);
			
			req.send();
		}
		else
		{
			form_init({title: ""});
		}
	},
	
	view_list: function() {
		var me = this,
			p_view = me.down("[name=p_view]"),
			notice_list = me.down("[name=notice_list]");
			
		p_view.hide();
		notice_list.show();
	},
	
	save_notice: function() {
		var me = this,
			p_view = me.down("[name=p_view]"),
			data = me._edit_item,
			has_err,
			d,
			dt_start,
			dt_end,
			req;
			
		has_err = p_view.getAllValues(data);
		
		if (has_err)
			return;
			
		data.content = me._editor.summernote("code");
		
		if (!data.content)
		{
			IG$.alertmsg(ig$.appname, "Content is required!", null, null, 0, "error");
			return;
		}
		
		d = data.dt_start;
		dt_start = d.getFullYear() + ((d.getMonth() + 1 < 10 ? "0" : "") + (d.getMonth() + 1)) + ((d.getDate() < 10 ? "0" : "") + d.getDate());
		
		d = data.dt_end;
		dt_end = d.getFullYear() + ((d.getMonth() + 1 < 10 ? "0" : "") + (d.getMonth() + 1)) + ((d.getDate() < 10 ? "0" : "") + d.getDate());
		
		data.fromdate = dt_start + (data.dt_start_hh < 10 ? "0" : "") + data.dt_start_hh + (data.dt_start_mm < 10 ? "0" : "") + data.dt_start_mm;
		data.todate = dt_end + (data.dt_end_hh < 10 ? "0" : "") + data.dt_end_hh + (data.dt_end_mm < 10 ? "0" : "") + data.dt_end_mm;
		
		if (Number(data.fromdate) >= Number(data.todate))
		{
			IG$.alertmsg(ig$.appname, "Date range error!", null, null, 0, "error");
			return;
		}
		
		req = new IG$._rpc$();
		req.init(null, 
			{
				ack: "notice",
				payload: {option: "save"},
				mbody: data,
				meta_type: "notice"
			}, null, function(jdoc) {
				me.view_list();
				me._load_notice();
			}
		);
		
		req.send();
	},
	
	preview_notice: function() {
		var me = this,
			p_view = me.down("[name=p_view]"),
			data = me._edit_item,
			has_err,
			d,
			dt_start,
			dt_end,
			req;
			
		has_err = p_view.getAllValues(data);
		
		if (has_err)
			return;
			
		data.content = me._editor.summernote("code");
		
		if (!data.content)
		{
			IG$.alertmsg(ig$.appname, "Content is required!", null, null, 0, "error");
			return;
		}
	},
	
	initComponent: function() {
		var me = this;
		
		$s.apply(this, {
			items: [
				{
					xtype: "panel",
					layout: {
						type: "vbox",
						align: "stretch"
					},
					padding: ig$.padding,
					flex: 1,
					items: [
						{
							xtype: "gridpanel",
							name: "notice_list",
							store: {},
							border: 0,
							flex: 1,
							columns: [
								{
									xtype: "gridcolumn",
									text: IRm$.r1("B_TITLE"),
									dataIndex: "title",
									tdCls: "igc-td-link",
									flex: 1
								},
								{
									text: IRm$.r1("B_DATE"),
									width: 150,
									sortable: false,
									dataIndex: "mdatefm"
								},
								{
									text: IRm$.r1("L_FR_DATE"),
									width: 150,
									dataIndex: "fromdate_fm"
								},
								{
									text: IRm$.r1("L_TO_DATE"),
									width: 150,
									dataIndex: "todate_fm"
								},
								{
									xtype: "gridcolumn",
									text: IRm$.r1("B_STATUS"),
									dataIndex: "active",
									width: 50
								},
								{
									xtype: "actioncolumn",
									width: 60,
									items: [
										{
											iconCls: "icon-grid-dbicon",
											handler: function (grid, rowIndex, colIndex) {
												var rec = grid.store.data.items[rowIndex];
												this.openDSLayer("cmd_edit_config", rec);
											},
											scope: this
										},
										{
											iconCls: "igc-share",
											tooltip: IRm$.r1("L_OBJECT_AUTH"),
											hidden: ig$.features["ig_n_oauth"] !== false, // || ig$.$l_ > 5,
											handler: function(grid, rowIndex, colIndex) {
												var rec = grid.store.data.items[rowIndex];
												this.openDSLayer("cmd_object_auth", rec);
											},
											scope: this
										},
										{
											iconCls: "icon-grid-delete",
											tooltip: IRm$.r1("L_REMOVE"),
											handler: function(grid, rowIndex, colIndex) {
												var me = this,
													store = grid.store,
													rec = store.getAt(rowIndex);

												IG$.deleteMetaObject(rec.get("uid"), rec, new IG$.callBackObj(me, function() {
													store.removeAt(rowIndex);
												}));
											},
											scope: this
										}
									]
								}
							],
							listeners: {
								cellclick: function(view, node, c, rec, rindex, e) {
									c == 0 && this.view_notice(rec);
								},
								scope: this
							},
							tbar: [
								{
									text: "Write",
									handler: function() {
										this.view_notice();
									},
									scope: this
								}								
							]
						},
						{
							xtype: "panel",
							hidden: true,
							name: "p_view",
							layout: {
								type: "vbox",
								align: "stretch"
							},
							items: [
								
								{
									xtype: "textfield",
									name: "title",
									required: true,
									fieldLabel: IRm$.r1("B_TITLE")
								},
								{
									xtype: "container",
									layout: {
										type: "hbox",
										align: "stretch"
									},
									items: [
										{
											xtype: "datefield",
											fieldLabel: "Start Date",
											required: true,
											name: "dt_start",
											width: 250
										},
										{
											xtype: "numberfield",
											minValue: 0,
											required: true,
											name: "dt_start_hh",
											maxValue: 23,
											width: 100,
											step: 1
										},
										{
											xtype: "numberfield",
											minValue: 0,
											required: true,
											name: "dt_start_mm",
											maxValue: 59,
											width: 100,
											step: 5
										},
										{
											xtype: "datefield",
											required: true,
											fieldLabel: "End Date",
											name: "dt_end",
											width: 250
										},
										{
											xtype: "numberfield",
											required: true,
											minValue: 0,
											name: "dt_end_hh",
											width: 100,
											maxValue: 23,
											step: 1
										},
										{
											xtype: "numberfield",
											minValue: 0,
											required: true,
											name: "dt_end_mm",
											width: 100,
											maxValue: 59,
											step: 5
										},
										{
											xtype: "container",
											flex: 1
										}
									]
								},
								{
									xtype: "container",
									layout: {
										type: "hbox",
										align: "stretch"
									},
									items: [
										{
											xtype: "numberfield",
											name: "width",
											required: true,
											width: 250,
											fieldLabel: IRm$.r1("B_WIDTH"),
											value: 300
										},
										{
											xtype: "numberfield",
											name: "height",
											width: 250,
											required: true,
											fieldLabel: IRm$.r1("B_HEIGHT"),
											value: 400
										},
										{
											xtype: "container",
											flex: 1
										}
									]
								},
								{
									xtype: "html",
									name: "d_content",
									flex: 1,
									html: "<div></div>"
								},
								{
									xtype: "container",
									layout: {
										type: "hbox",
										align: "stretch"
									},
									items: [
										{
											xtype: "button",
											text: "Preview",
											handler: function() {
												this.preview_notice();
											},
											scope: this
										},
										{
											xtype: "container",
											flex: 1
										},
										{
											xtype: "button",
											text: "Confirm",
											handler: function() {
												this.save_notice();
											},
											scope: this
										},
										{
											xtype: "button",
											text: "Cancel",
											handler: function() {
												this.view_list();
											},
											scope: this
										},
										{
											xtype: "container",
											flex: 1
										}
									]
								}
							]
						}
					]
				}
			],
			listeners: {
				afterrender: function() {
					this.init_f();
				},
				scope: this
			}
		});
		
		IG$.mgrNotice.superclass.initComponent.call(this);
	}
});