IG$.nlpEdItCr = $s.extend($s.window, {
	
	modal: true,
	layout: "fit",
	closable: false,
	resizable:false,
	width: 600,
	height: 500,
	
	title: "Crawler Content",
	
	init_f: function() {
		var me = this,
			rec = me._mc,
			grd_crawl_item = me.down("[name=grd_crawl_item]"),
			p_main = me.down("[name=p_main]"),
			html_contents = rec ? rec.get("html_contents") : null;
		
		if (rec)
		{
			p_main.setAllValues(rec.data, false);
			grd_crawl_item.store.loadData(html_contents);
		}
		else
		{
			me.down("[name=absolute_url]").setValue(me.crawl_base_url);
		}
	},

	_tbHandler: function(cmd, opt) {
		var me = this;

		switch(cmd)
		{
		case "cmd_edit":
			me._edit(opt);
			break;
		case "cmd_run":
			me._run();
			break;
		}
	},

	_edit: function(rec) {
		var me = this;

		dlg = new IG$.nlpEdItCrT({
			instance: me.instance,
			_mc: rec,
			callback: new IG$.callBackObj(me, function(data) {
			})
		});
		IG$.showDlg(me, dlg);
	},

	_run: function() {
		var me = this,
			absolute_url = me.down("[name=absolute_url]"),
			req = new IG$._rpc$();
		
		req.init(me, 
			{
				ack: "collaborate",
				payload: {cmd: "crawl_run"},
				mbody: {
					absolute_url: absolute_url.getValue()
				}
			}, me, function(jdoc) {
				var html_contents = jdoc.html_contents,
					rec = me._mc,
					grd_crawl_item = me.down("[name=grd_crawl_item]");

				grd_crawl_item.store.loadData(html_contents);
			}, null);
		req.send();
	},
	
	_confirm: function() {
		var me = this,
			rec = me._mc,
			html_contents = [],
			grd_crawl_item = me.down("[name=grd_crawl_item]"),
			store, i,
			r, s;

		store = grd_crawl_item.store;

		for (i=0; i < store.data.items.length; i++)
		{
			r = store.data.items[i];

			s = {
				title: r.get("title"),
				content: r.get("content")
			};

			html_contents.push(s);
		}

		if (rec)
		{
			rec.set("html_contents", html_contents);
		}
		else
		{
			me._mc = {
				absolute_url: me.down("[name=absolute_url]").getValue(),
				html_contents: html_contents
			};
		}
		
		me.callback && me.callback.execute(me._mc, me._mc);
		me.close();
	},
	
	initComponent : function() {
		var me = this;
		
		$s.apply(me, {
			padding: ig$.padding,
			border: 0,
			items: [
				{
					xtype: "panel",
					layout: "fit",
					padding: ig$.padding,
					autoScroll: true,
					items: [
						{
							xtype: "panel",
							layout: {
								type: "vbox",
								align: "stretch"
							},
							name: "p_main",
							padding: ig$.padding,
							items: [
								{
									xtype: "textfield",
									name: "absolute_url",
									fieldLabel: IRm$.r1("B_URL")
								},
								{
									xtype: "gridpanel",
									flex: 1,
									name: "grd_crawl_item",
									store: {},
									selType: "checkboxmodel", // ig$.$l_ > 5 ? null : 
									selModel: {
										// checkSelector: ".x-grid-cell"
									},
									columns: [
										{
											text: IRm$.r1("B_TITLE"), // "Intent",
											dataIndex: "title",
											tdCls: "igc-td-link",
											flex: 1
										},
										{
											text: IRm$.r1("B_CONTENT"), // "Type",
											dataIndex: "content",
											width: 280
										},
										{
											xtype: "actioncolumn",
											width: 60,
											items: [
												{
													iconCls: "icon-grid-dbicon",
													handler: function (grid, rowIndex, colIndex) {
														var rec = grid.store.data.items[rowIndex];
														this._tbHandler("cmd_edit", rec);
													},
													scope: this
												},
												{
													iconCls: "icon-grid-delete",
													tooltip: IRm$.r1("L_REMOVE"),
													handler: function(grid, rowIndex, colIndex) {
														var me = this,
															store = grid.store,
															rec = store.getAt(rowIndex);
														store.removeAt(rowIndex);
													},
													scope: this
												}
											]
										}
									],
									listeners: {
										cellclick: function(view, node, c, rec, rindex, e) {
											this._tbHandler("cmd_edit", rec);
										},
										scope: this
									}
								}
							]
						}
					]
				}
									
			],
			buttons:[
				{
					text: IRm$.r1('L_CRAWL_RUN'),
					bodyCls: "ig-btn-confirm",
					handler: function() {
						this._tbHandler("cmd_run");
					},
					scope: this
				},
				"->",
				{
					text: IRm$.r1('B_CONFIRM'),
					bodyCls: "ig-btn-confirm",
					handler: function() {
						this._confirm();
					},
					scope: this
				}, 
				{
					text: IRm$.r1('B_CANCEL'),
					handler:function() {
						this.close();
					},
					scope: this
				}
			],

			listeners: {
				afterrender: function() {
					this.init_f();
				}
			}
		});
		
		IG$.nlpEdItCr.superclass.initComponent.apply(this, arguments);
	}
});

IG$.nlpEdItCrT = $s.extend($s.window, {
	
	modal: true,
	layout: "fit",
	closable: false,
	resizable:false,
	width: 600,
	height: 500,
	
	title: "Crawler Content",
	
	init_f: function() {
		var me = this,
			rec = me._mc,
			p_main = me.down("[name=p_main]");

		p_main.setAllValues(rec.data, false);
	},
	
	_confirm: function() {
		var me = this,
			rec = me._mc;

		rec.set("title", me.down("[name=title]").getValue());
		rec.set("contennt", me.down("[name=content]").getValue());
		
		me.callback && me.callback.execute(me._mc);
		me.close();
	},
	
	initComponent : function() {
		var me = this;
		
		$s.apply(me, {
			padding: ig$.padding,
			border: 0,
			items: [
				{
					xtype: "panel",
					layout: "fit",
					padding: ig$.padding,
					autoScroll: true,
					items: [
						{
							xtype: "panel",
							layout: {
								type: "vbox",
								align: "stretch"
							},
							name: "p_main",
							padding: ig$.padding,
							items: [
								{
									xtype: "textfield",
									name: "title",
									fieldLabel: IRm$.r1("B_TITLE")
								},
								{
									xtype: "textarea",
									name: "content",
									fieldLabel: IRm$.r1("B_CONTENT")
								}
							]
						}
					]
				}
									
			],
			buttons:[
				"->",
				{
					text: IRm$.r1('B_CONFIRM'),
					bodyCls: "ig-btn-confirm",
					handler: function() {
						this._confirm();
					},
					scope: this
				}, 
				{
					text: IRm$.r1('B_CANCEL'),
					handler:function() {
						this.close();
					},
					scope: this
				}
			],

			listeners: {
				afterrender: function() {
					this.init_f();
				}
			}
		});
		
		IG$.nlpEdItCrT.superclass.initComponent.apply(this, arguments);
	}
});