	_confirm: function() {
		var me = this,
			pnl_auth = me.down("[name=pnl_auth]"),
			rest_auth = {};
		
		if(pnl_auth.getAllValues(rest_auth))
			return;
			
		me.callback && me.callback.execute(rest_auth);
		me.close();
	},
	
